import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

let existInLocalStorage = localStorage.getItem('loggedInForDashboard');
export default new Vuex.Store({
  state: {
    loggedIn: (existInLocalStorage === "true") || false,
    lang: "en"
  },
  getters: {
    isLogged : state => state.loggedIn,
  },
  mutations: {
    toggleLang: state => {
      if(state.lang === "en"){
        state.lang = "ar"
      }else{
        state.lang = "en"
      }
    },
    userLogIn: state => {
      state.loggedIn = true;
      localStorage.setItem('loggedInForDashboard', state.loggedIn)
    },
    userLogOut: state => {
      state.loggedIn = false
      localStorage.removeItem('loggedInForDashboard')
    },
  },
  actions: {
    toggleLang({commit}){
      commit('toggleLang')
    },
    userLogIn({commit}){
      commit("userLogIn")
    },
    userLogOut({commit}){
      commit("userLogOut")
    }
  },
});
